main {
  /* margin-top: 10vh; */
  /* background-color: #6f83f5; */
  transform: skewY(-6deg);
  box-shadow: 2px 2px 10px 1px black;
  position: relative;
}

.block {
  transform: skewY(6deg);
}

.Portfolio {
  background-Position: center;
  background-Size: cover;
  background-Repeat: no-repeat;
}

.Portfolio__background {
  width: 100%;
  padding-bottom: 100px;
}

.Portfolio-container {
  margin: 0;
  height: 60vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  object-fit: contain;
  margin-bottom: 41px;
}

.Portfolio__row {
  padding-top: 0!important;
  padding-bottom: 0!important;
}

.Portfolio-container>h1 {
  color: #fff;
  font-size: 100px;
}

.Typewriter {
  margin-top: 8px;
  color: #fff;
  font-size: 32px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.clients {
  width: 80%;
  margin: auto;
  padding-top: 40px;
  padding-bottom: 40px;
}

.client {
  padding: 20px;
}

.client img {
  width: 100%;
  height: 100%;
  /* border: black solid 1px; */
  /* box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.2); */
}

.clientBtn {
  padding: 0px;
}

.clientBtn .details {
  padding: 20px;
  /* margin: 20px; */
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  color: black;
  opacity: 0;
}


.Portfolio__client__name{
padding-top: 10%;
font-weight: bold;
}

.Portfolio__client__industry{
  font-weight: bold;
  padding-top: 10%;
}


.Portfolio__client__description{
  padding-top: 10%;
}

.details:hover {
  background: #ffffff;
  color: black;
  opacity: 0.8;
  border-radius: 5px;
  -webkit-transition: .3s ease-in-out;
transition: 0.5s ease-in-out;

}

@media screen and (max-width: 960px) {
  .Portfolio-container>h1 {
    font-size: 70px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 768px) {
  .Portfolio-container>h1 {
    font-size: 50px;
    margin-top: -100px;
  }
  .Portfolio-container>p {
    font-size: 30px;
  }
}